import React, { PureComponent } from 'react';
import {Button} from 'antd';
class NoBimPage extends PureComponent {
    //
    switchModel = ()=>{
        this.props.switchModels();
    }
    render() { 
        return ( 
            <div>
                <div className='model-picture'></div>
                <div className='model-title'>
                    <p  style={{display:'inline-block'}}>{this.props.title}</p>
                    <Button type='primary' onClick={this.switchModel}>BIM</Button>
                </div>
            </div>
         );
    }
}
 
export default NoBimPage;