import React, { PureComponent } from 'react';
import {connect} from "react-redux";
import {Button, Col, Form, Input, message, Modal, Row, Select, Table,Icon,TreeSelect,Tree} from 'antd';
import {BimModel} from "./style";

import NoBimPage from './noBimPage';
import ThreedView from './threeView';
import './bim-model.css';
import * as type from "../../../axios/index";
const { TreeNode } = TreeSelect;
let motorToken = '';
class bimModel extends PureComponent {
    state = {  
        bimVisible:false,
        treeValue:undefined,
        init3dView:false,
        siteSwitchVisible:false,
        checkedKeys:[],
        selectedKeys: [],
        siteList:[],
        bimTreeData:[],//BIM
        selectBimProject:'',//
        selectBimInfo:{
            projectId:'',
            motor3dId:'',
           /*  projectId:'ac7ee924-bd9b-4628-9436-de90a083dabc',
            appid:"d0b3c61c6639434e84900b1fd8d391cb",
            secret:"459dc8b77a63a0c009aec27f818febf6", */
        },
        currentTreeNode:{},
    }

    //
    getDeptInfo = () => {
        type.getDeptInfo().then((data) => {
            if(data.data.code === 1 && data.data.data.hasOwnProperty('deptId')) {
                data.data.data['id'] = data.data.data.deptId;
                data.data.data['pId'] = 0;
                data.data.data['value'] = data.data.data.deptId;
                data.data.data['title'] =  (<span><span className='icon-dept'></span>{data.data.data.deptName}</span>);
                data.data.data['disabled'] = true;
                data.data.data['key'] = data.data.data.deptId;
                this.getListDeptNode(data.data.data);
            }
        })
    }
    //
    getListDeptNode = (parent) => {
        type.getListDeptNode().then((data) => {
            if(data.data.code === 1 && data.data.data.hasOwnProperty('projectList')) {
                let directlyPpidList = [];
                let noDirectlyPpidList = [];

                Object.keys(data.data.data).map((keys) => (
                    data.data.data[keys].map((val,key) => {
                        if(val.parentId){
                            val.id = val.nodeId;
                            val.pId = val.parentId;
                            val['disabled'] = true;
                            val.value = val.nodeId;
                            val.key = val.nodeId;
                            if (val.nodeType === 1) {
                                val.title = (<span ><span className='icon-biao'></span>{val.nodeName}</span>);
                            } else if (val.nodeType === 2) {
                                val.title = (<span ><span className='icon-xiang'></span>{val.nodeName}</span>);
                            } else if (val.nodeType === 3) {
                                val.title = (<span ><span className='icon-dan'></span>{val.nodeName}</span>);
                            }
                        }else{
                            val.id = val.ppid;
                            val.pId = val.nodeId;
                            val.value = val.ppid;
                            val.key = val.ppid;
                            if (val.projTypeStr === '') {
                                val.title = (<span ><span className='icon-project icon-tujian'></span>{val.projectName}</span>);
                            }else if (val.projTypeStr === '') {
                                val.title = (<span ><span className='icon-project icon-gangjin'></span>{val.projectName}</span>);
                            } else if (val.projTypeStr === '') {
                                val.title = (<span ><span className='icon-project icon-zaojia'></span>{val.projectName}</span>);
                            } else if (val.projTypeStr === '') {
                                val.title = (<span ><span className='icon-project icon-anzhuang'></span>{val.projectName}</span>);
                            } else if (val.projTypeStr === '') {
                                val.title = (<span ><span className='icon-project icon-changbu'></span>{val.projectName}</span>);
                            } else if (val.projTypeStr === 'Tekla') {
                                val.title = (<span ><span className='icon-project icon-tekla'></span>{val.projectName}</span>);
                            } else if (val.projTypeStr === 'Revit') {
                                val.title = (<span ><span className='icon-project icon-revit'></span>{val.projectName}</span>);
                            } else if (val.projTypeStr === '') {
                                val.title = (<span ><span className='icon-project icon-banzhu'></span>{val.projectName}</span>);
                            } else if (val.projTypeStr === 'Civil3D') {
                                val.title = (<span ><span className='icon-project icon-civil3D'></span>{val.projectName}</span>);
                            } else if (val.projTypeStr === 'Bentley') {
                                val.title = (<span ><span className='icon-project icon-bentley'></span>{val.projectName}</span>);
                            } else if (val.projTypeStr === 'Rhino') {
                                val.title = (<span ><span className='icon-project icon-rhino'></span>{val.projectName}</span>);
                            } else if (val.projTypeStr === 'IFC') {
                                val.title = (<span ><span className='icon-project icon-ifc'></span>{val.projectName}</span>);
                            }

                            if(val.nodeType === 0 || val.nodeType === null) {
                                directlyPpidList.push(val)
                            } else {
                                noDirectlyPpidList.push(val);
                            }
                        }
                    })
                ))

                let obj = data.data.data.deptUnitList.concat(directlyPpidList).concat(noDirectlyPpidList).concat([parent]);
                this.setState({bimTreeData:obj});
            }
        })
    }
    treeNodeFilter = (inputValue,treeNode) =>{
        console.log(inputValue,treeNode)
        if(treeNode.props.hasOwnProperty("projectName")) {
            if(treeNode.props.projectName.indexOf(inputValue) != -1){
                return true;
            }
        } else if(treeNode.props.hasOwnProperty("nodeName")){
            if(treeNode.props.nodeName.indexOf(inputValue) != -1){
                return true;
            }
        }
        return false;
    }
    //BIM[V1.1]
    putBeatBim = (params) => {
        return new Promise(function(reslove,reject){
            type.putBeatBim(params).then((data)=>{
                if(data.data.code === 1) {
                    reslove(data.data);
                }
            })
        })
    }
    //token
    getMotorToken = () => {
        type.getMotorToken().then((data)=>{
            if(data.data.code == 1) {
                motorToken = data.data.data.token
            }
        })
    }
    // BIM
    switchModels = ()=> {
        this.getDeptInfo();
        this.setState({bimSwitchVisible:true})
    }
    
    //bim
    onHandelChange = (value, label, extra)=> {
        if (value === undefined) {
            this.setState({treeValue:undefined});
        } else {
            if (extra.triggerNode.props.hasOwnProperty('ppid')){
                this.putBeatBim({ppid:value}).then((data)=>{
                    if(data.code === 1 && data.data.result) {
                        this.setState({selectBimProject:value,currentTreeNode:extra.triggerNode.props,treeValue:value,selectBimInfo:{projectId:extra.triggerNode.props.projectId,motor3dId:extra.triggerNode.props.motor3dId}});
                        // this.setState({selectBimProject:value,currentTreeNode:extra.triggerNode.props,treeValue:value});
                    } else {
                        message.error(data.msg);
                    }
                })
            }
        }
    }
    //ok
    handleBimSwitchOk = ()=> {
        if(this.state.treeValue === undefined || this.state.selectBimInfo.motor3dId === null) {
            if ( this.state.selectBimInfo.motor3dId === null) {
                message.error('');

            }
            return false;
        }
        this.setState({
            bimSwitchVisible: false,
            init3dView:true,
            treeValue:undefined
        });
    }
    //
    handleBimSwitchCancel = ()=> {
        this.setState({
            bimSwitchVisible: false,
            treeValue:undefined
        });
    }
    //BIM
    switchSiteModal = ()=>{
        this.setState({siteSwitchVisible:true});
    }
    onExpand = expandedKeys => {
        console.log('onExpand', expandedKeys);
        // if not set autoExpandParent to false, if children expanded, parent can not collapse.
        // or, you can remove all expanded children keys.
        this.setState({
          expandedKeys,
          autoExpandParent: false,
        });
      };
    
      onCheck = (checkedKeys,treeNode) => {
        // console.log('onCheck', checkedKeys);
        this.siteList = treeNode.checkedNodes;
        this.setState({ checkedKeys });
      };
    
      onSelect = (selectedKeys, info) => {
        this.setState({ selectedKeys });
      };
    
      renderTreeNodes = data =>
        data.map(item => {
          if (item.children) {
            return (
              <TreeNode value= {item.title} title={item.title} key={item.key} dataRef={item}>
                {this.renderTreeNodes(item.children)}
              </TreeNode>
            );
          }
          return <TreeNode key={item.key} {...item} />;
        });
        
    //ok
    handleSiteSwitchOk = ()=> {
        let siteList = this.siteList;
        this.setState({
            siteList: this.siteList,
            siteSwitchVisible: false,
        },()=>{
            //console.log(this.state.siteList)
        });
        
    }
    renderSiteItem = data=> 
        data.map((item,key) => {
            return <p key={key}>{item.props.title}<span className='icon-delete' onClick={()=>this.removeSiteItem(item,key)}></span></p>
        })
     
    removeSiteItem = (item,key)=>{
        const {siteList}  = this.state;
        let siteListCopy = _.cloneDeep(siteList);
        siteListCopy.splice(key,1);
        this.setState({
            siteList:siteListCopy
        })
    }
    //
    handleSiteSwitchCancel = ()=> {
        this.setState({
            siteSwitchVisible: false,
        });
    }
    //
    componentDidMount(){
        // var obj = new Runoob.Calc();
        this.getMotorToken();
    }
    render() { 
        const formItemLayout = {
            labelCol: {
              xs: { span: 24 },
              sm: { span: 5 },
            },
            wrapperCol: {
              xs: { span: 24 },
              sm: { span: 19 },
            },
        };
        return (  
                <BimModel >
                    {
                        !this.state.init3dView ?(
                            <Row className='bim-model'>
                                <NoBimPage title="BIM" switchModels={this.switchModels} />
                            </Row>
                        ):<ThreedView bimProject = {this.state.selectBimProject} selectBimInfo = {this.state.selectBimInfo} treeNode = {this.state.currentTreeNode} token = {motorToken}></ThreedView>
                    }
                    
                    <Modal className='select-modal'
                        title="BiM"
                        maskClosable ={false}
                        visible={this.state.bimSwitchVisible}
                        onOk={this.handleBimSwitchOk}
                        onCancel={this.handleBimSwitchCancel}
                    >
                        <Form {...formItemLayout} onSubmit={this.handleBimSwitchOk}>
                            <Form.Item label="BIM" className='form-item'>
                                <TreeSelect
                                    treeDataSimpleMode
                                    showSearch
                                    style={{ width: '100%' }}
                                    value={this.state.treeValue}
                                    dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                                    placeholder=""
                                    allowClear
                                    treeDefaultExpandAll
                                    filterTreeNode={this.treeNodeFilter}
                                    dropdownClassName = 'bim-tree'
                                    treeData={this.state.bimTreeData}
                                    onChange={this.onHandelChange}
                                > 

                                {/*  treeData={this.state.bimTreeData}
                                    
                                    {this.renderTreeNodes(this.state.bimTreeData)}*/}
                                </TreeSelect>
                            </Form.Item>
                            <Form.Item label="BIM" className='form-item'>
                                <Button className='bim-btn-default' disabled onClick={this.switchSiteModal}></Button>
                            </Form.Item>
                            <Form.Item >
                                {
                                    this.state.siteList.length>0?
                                    (
                                    <div className='site-item'>
                                        {this.renderSiteItem(this.state.siteList)}
                                    </div>):''
                                }
                            </Form.Item>
                        </Form>
                    </Modal>
                    <Modal className='site-modal'
                        title=""
                        visible={this.state.siteSwitchVisible}
                        onOk={this.handleSiteSwitchOk}
                        onCancel={this.handleSiteSwitchCancel}
                    >
                     <Tree
                        checkable
                        onExpand={this.onExpand}
                        onCheck={this.onCheck}
                        checkedKeys={this.state.checkedKeys}
                        onSelect={this.onSelect}
                        selectedKeys={this.state.selectedKeys}
                        defaultExpandAll = {true}
                    >
                        {this.renderTreeNodes(this.state.bimTreeData)}
                    </Tree>
                    </Modal>
                </BimModel>
            );
    }
}
 
export default bimModel;