// 
function reSetNodes(zTreeObj) {
    let node = zTreeObj.getNodes();
    node.forEach((rootnode)=>{
        rootTreeNodes.push(rootnode);
    })
    let nodes = zTreeObj.transformToArray(node);
    if (nodes.length > 0) {
        for (let i = 0; i < nodes.length; i++) {
            if (!nodes[i].children) {
                if (nodes[i].level === 3) {
                    nodes[i].isParent = true;
                }
                nodes[i].nocheck = true;
                zTreeObj.updateNode(nodes[i]);
            }
        }
    }
}
function convertCanvasToImage(canvas) {
    //ImageDOM
    var image = new Image();
    // canvas.toDataURL Base64URL
    //  PNG
    image.src = canvas.toDataURL("image/png");
    return image.src;
}
function rendImage(url,context) {
    // let createImg = document.createElement('img');
    let bgImg = new Image();
        bgImg.setAttribute('src', url);
    return bgImg;
}
 function dealWords(options) {
    var allRow = Math.ceil(options.ctx.measureText(options.word).width / options.maxWidth);//
    var count = allRow >= options.maxLine ? options.maxLine : allRow;//
        
    var endPos = 0;//
    for (var j = 0; j < count; j++) {
        var nowStr = options.word.slice(endPos);//
        var rowWid = 0;//    
        if (options.ctx.measureText(nowStr).width > options.maxWidth) {//
            for (var m = 0; m < nowStr.length; m++) {
                rowWid += options.ctx.measureText(nowStr[m]).width;//
                if (rowWid > options.maxWidth) {                        
                    if (j === options.maxLine - 1) { //
                        options.ctx.fillText(nowStr.slice(0, m - 1) + '...', options.x, options.y + (j + 1) * 18);    //(j+1)*18        
                    } else {
                        options.ctx.fillText(nowStr.slice(0, m), options.x, options.y + (j + 1) * 18);
                    }
                    endPos += m;//
                    break;
                }
            }
        } else {//
            options.ctx.fillText(nowStr.slice(0), options.x, options.y + (j + 1) * 18);
        }
    }
}

// Converts image to canvas; returns new canvas element
function convertImageToCanvas(image,status,name,icon) {
    let canvas = document.createElement("canvas");
    let context = canvas.getContext("2d");
	canvas.width = 117;
    canvas.height = 74;
    canvas.title = name;
    // 
    canvas.getContext("2d").drawImage(rendImage(image), 0, 0);
    canvas.getContext("2d").drawImage(rendImage(icon), 10, 10);
    context.font="12px microsoft yahei";
    context.fillStyle='#fff';
    context.maxWidth = '100px';
    context.fillText(status,35,23);
    // context.fillText(name,10,50);

    dealWords({ ctx: context,//
        fontSize: 12,//
        word: name+"",//
        maxWidth: 100,//
        x: 10,//x
        y: 30,//y
        maxLine: 1//
    })
    /* canvas.onmousemove = function(e) {
        console.log(777778)
    } */
    return convertCanvasToImage(canvas)
}

